@import url(https://fonts.googleapis.com/css2?family=Lato:wght@100;300;400;700&display=swap);
*{box-sizing:border-box;margin:0;padding:0}html{font-size:62.5%}body{font-size:1.6rem}p,i,pre,textarea,input,button{font-family:'Lato', sans-serif}#root{align-items:stretch;display:flex;flex-direction:row;justify-content:center;width:100%}.app{margin:2% 0;width:50rem}.main-section{margin-top:2%}

.post-generator-wrap{box-shadow:0 0 3px grey;padding:1rem}.insert-post-section{display:grid;grid-template-rows:100%;grid-template-columns:1.5fr 10.5fr;border:none;box-shadow:0 1px lightgrey;margin:1% 0 2% 0;padding-bottom:1.5rem}.insert-post-section>:first-child{margin:0 auto}.profile-picture{border-radius:50%;height:40px;width:40px}.text-area-autosize-post{border:none;font-size:2rem;height:100%;outline:none;padding:1rem;resize:none;width:100%}.btn-post{background-color:#196ecf;border:none;border-radius:.3rem;color:white;font-size:1.6rem;font-weight:600;outline:none;padding:.5rem;width:100%}.btn-post:hover{background-color:#135bad;cursor:pointer}

.show-comment-tool-menu{color:#c7c5c5 !important;display:flex !important;visibility:visible !important}.comment-wrap{display:grid;grid-template-rows:100%;grid-template-columns:9% 91%;margin:.5rem 0 1rem 0}.comment-wrap>:first-child{align-items:stretch;display:flex;flex-direction:row;justify-content:center}.profile-picture-comments{border-radius:50%;height:30px;width:30px}.comment{align-items:stretch;display:flex;flex-direction:column;justify-content:flex-start;border-radius:10px}.comment>:first-child{align-items:stretch;display:flex;flex-direction:row;justify-content:flex-start}.comment>:first-child pre{border-radius:1.5rem;background-color:#eae9e9;display:inline-block;padding:1rem;white-space:pre-wrap;word-break:break-all}.comment>:first-child pre,.comment>:first-child span{font-size:1.3rem}.comment>:first-child span{color:#104b8e;font-weight:bold;margin-right:.5rem;margin-bottom:.3rem}.comment>:first-child span:hover{box-shadow:0 1px #104b8e;cursor:pointer}.comment>:first-child:hover .comment-tool-menu-wrap>:first-child{cursor:pointer;color:#c7c5c5;visibility:visible}.comment-tool-menu-wrap{position:relative}.comment-tool-menu-wrap>:first-child{font-size:1.6rem;padding:1rem;visibility:hidden}.comment-tool-menu-wrap>:last-child{align-items:flex-start;display:none;flex-direction:column;justify-content:space-between;border:.1px solid grey;border-radius:5px;background-color:white;box-shadow:0 0 10px lightgrey;font-size:1.6rem;right:0;position:absolute;z-index:50}.comment-tool-menu-wrap>:last-child p{align-items:center;display:flex;flex-direction:row;justify-content:flex-start;color:black;font-size:1.2rem;padding:.7rem 1rem .7rem .5rem;width:100%}.comment-tool-menu-wrap>:last-child p i{color:dimgrey;margin:0 1rem}.comment-tool-menu-wrap>:last-child p:hover{cursor:pointer;color:white;background-color:#196ecf}.comment-tool-menu-wrap>:last-child p:hover i{color:white}.comment-menu-option{align-items:initial;display:flex;flex-direction:row;justify-content:flex-start;padding-left:1rem}.comment-menu-option p,.comment-menu-option span{font-size:1.3rem;margin:.2rem}.comment-menu-option p{color:#104b8e}.comment-menu-option p:hover{box-shadow:0 1px #104b8e;cursor:pointer}.comment-menu-option span{color:#565555}.comment-menu-option>:last-child:hover{box-shadow:0 1px grey;cursor:pointer}

.blue_color{color:#196ecf !important}.article{box-shadow:0 0 3px grey;margin-top:2%}.main-wrap-post{padding:1rem 1rem .5rem 1rem}.account-info{display:grid;grid-template-rows:100%;grid-template-columns:10% 90%;margin-bottom:2%}.account-info>:last-child h3{color:#104b8e;padding-bottom:.1rem}.account-info>:last-child h3 a:hover{box-shadow:0 1px #104b8e;cursor:pointer}.account-info>:last-child small{color:#565555;font-style:italic;padding-bottom:.3rem}.account-info>:last-child small:hover{box-shadow:0 1px grey;cursor:pointer}.profile-picture{border-radius:50%;height:40px;width:40px}.post-content{margin-bottom:3%}.post-content pre{word-break:break-all;white-space:pre-wrap}.post-info{display:grid;grid-template-rows:100%;grid-template-columns:40% 60%;margin-bottom:1.5%}.post-info div{align-items:center;display:flex;flex-direction:row;justify-content:flex-start}.post-info>:first-child{justify-content:left}.post-info>:first-child p{color:#565555;font-size:1.4rem;padding-left:.2rem}.post-info>:first-child p:hover{box-shadow:0 1px grey;cursor:pointer}.post-info>:first-child i{border-radius:50%;background-color:#196ecf;color:white;font-size:1rem;padding:.4rem}.post-info :last-child{align-items:center;display:flex;flex-direction:row;justify-content:space-around}.post-info :last-child p{color:#565555;font-size:1.4rem}.post-info :last-child p:hover{box-shadow:0 1px grey;cursor:pointer}.menu-interaction{align-items:center;display:flex;flex-direction:row;justify-content:space-around;box-shadow:0 -1px lightgrey;padding-top:1%}.menu-interaction div{border-radius:3px;color:#565555;font-weight:bold;padding:1.2%;text-align:center;width:30%}.menu-interaction div:hover{background-color:lightgrey;cursor:pointer}.menu-interaction i{font-size:1.6rem}.main-wrap-comments{box-shadow:0 -1px lightgrey;padding:.5rem 1rem}.add-comment-form{margin:2% 0;width:auto}.add-comment-form div{display:grid;grid-template-rows:none;grid-template-columns:8% 92%}.text-area-autosize-comment{background-color:#eae9e9;border:1px solid lightgrey;border-radius:15px;font-size:1.3rem;outline:none;padding:.6rem 1rem;resize:none}

